import { Object } from 'core-js'

export default {
  el (state, icon) {
    state.selected = icon.icon.path
    if (window.localStorage.getItem('userinfo')) {
      icon.rou.history.push('/cart')
    } else {
      icon.rou.history.push(icon.icon.path)
    }
  },
  addToCart (state, cartItem) {
    const isInCart = state.cart.some(item => item.id === cartItem.id)
    if (isInCart) {
      state.cart = state.cart.map(item => {
        const newItem = Object.assign({}, item)
        if (newItem.id === cartItem.id) {
          newItem.count++
        }
        return newItem
      })
    } else {
      state.cart.push({
        count: 1,
        ...cartItem
      })
    }
    window.localStorage.setItem('l-cart', JSON.stringify(state.cart))
  },
  changeCartCountByIdAndType (state, payload) {
    state.cart = state.cart.map(item => {
      const newItem = Object.assign({}, item)
      console.log(newItem)
      if (newItem.id === payload.id) {
        if (payload.type === 'unIncrease') {
          newItem.count--
          if (newItem.count < 0) {
            newItem.count = 0
          }
        } else {
          newItem.count++
        }
      }
      return newItem
    })
    window.localStorage.setItem('l-cart', JSON.stringify(state.cart))
  },
  delete (state, payload) {
    state.cart = state.cart.map(item => {
      const newItem = Object.assign({}, item)
      if (newItem.id === payload.id) {
        if (payload.type === 'delete') {

        }
      }
    })
  }
}



