import fenleiChildren from './fenleiChildren'

const Home = () => import('@/pages/Home')
const Cart = () => import('@/pages/Cart')
const Fenlei = () => import('@/pages/Fenlei')
const Account = () => import('@/pages/Account')

const Gaogenxie = () => import('@/pages/homelink/Gaogenxie')
const Notebook = () => import('@/pages/homelink/Notebook')
const Huzi = () => import('@/pages/homelink/Huzi')
const Lingdai = () => import('@/pages/homelink/Lingdai')
const Shangjia = () => import('@/pages/homelink/Shangjia')
const Wenda = () => import('@/pages/homelink/Wenda')

const Businesslist = () => import('@/pages/Businesslist/Businesslist')
const Catalog = () => import('@/pages/catalog/Catalog')
const Detail = () => import('@/pages/detail/Detail')

const Successcom = () => import('@/pages/success/Success')

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    istabbar: true,
    title: '主页',
    icon: 'shouye'
  },
  {
    path: '/fenlei',
    name: 'fenlei',
    redirect: '/fenlei/drug',
    component: Fenlei,
    istabbar: true,
    title: '分类',
    icon: 'fenlei',
    icons: 'fenlei1',
    tit: '商品分类',
    ishomelink: true,
    children: fenleiChildren
  },
  {
    path: '/cart',
    name: 'cart',
    component: Cart,
    istabbar: true,
    title: '购物车',
    icon: 'gouwuche'
  },
  {
    path: '/account',
    name: 'account',
    component: Account,
    istabbar: true,
    title: '账号',
    icon: 'wode',
    tit: '我的订单',
    icons: 'dingdan',
    ishomelink: true
  },
  {
    path: '/notebook',
    name: 'notebook',
    component: Notebook,
    icons: 'notebook',
    tit: '疾病知识',
    ishomelink: true
  },
  {
    path: '/lingdai',
    name: 'lingdai',
    component: Lingdai,
    icons: 'lingdai',
    tit: '男性',
    ishomelink: false
  },
  {
    path: '/gaogenxie',
    name: 'gaogenxie',
    component: Gaogenxie,
    icons: 'gaogenxie',
    tit: '女性',
    ishomelink: false
  },
  {
    path: '/huzi',
    name: 'huzi',
    component: Huzi,
    icons: 'huzi',
    tit: '老人',
    ishomelink: false
  },
  {
    path: '/shangjia',
    name: 'shangjia',
    component: Shangjia,
    icons: 'shangjia',
    tit: '商家入驻',
    ishomelink: false
  },
  {
    path: '/wenda',
    name: 'wenda',
    component: Wenda,
    icons: 'wenda',
    tit: '健康问答',
    ishomelink: true
  },
  {
    path: '/Businesslist/:aid',
    name: 'xiangqing',
    component: Businesslist
  },
  {
    path: '/catalog/:aid',
    name: 'catalog',
    component: Catalog
  },
  {
    path: '/detail/:aid',
    name: 'detail',
    component: Detail
  }, {
    path: '/success',
    name: 'success',
    component: Successcom
  }
]

export default routes



