const Drug = () => import('@/pages/children/Drug')
const Health = () => import('@/pages/children/Health')
const Medical = () => import('@/pages/children/Medical')
const Plan = () => import('@/pages/children/Plan')
const Medicine = () => import('@/pages/children/Medicine')
const Beauty = () => import('@/pages/children/Beauty')

const fenleiChildren = [
  {
    path: 'drug',
    name: 'drug',
    component: Drug,
    LeftTitle: '中医药品'
  }, {
    path: 'health',
    name: 'health',
    component: Health,
    LeftTitle: '养生保健'
  }, {
    path: 'medical',
    name: 'medical',
    component: Medical,
    LeftTitle: '医疗器械'
  }, {
    path: 'plan',
    name: 'plan',
    component: Plan,
    LeftTitle: '计生用品'
  }, {
    path: 'medicine',
    name: 'medicine',
    component: Medicine,
    LeftTitle: '中药饮料'
  }, {
    path: 'beauty',
    name: 'beauty',
    component: Beauty,
    LeftTitle: '美容护肤'
  }
]
export default fenleiChildren



