<template>
    <div id="app">
    <div class="header">
        <div class="header-left"><i class="iconfont icon-yiyao"></i></div>
        <div class="header-title">
          <div class="wrap">
            <i class="iconfont icon-search"></i>
            <input type="text" class="search" placeholder="批准文号、通用名、商品名、症状"/>
          </div>
        </div>
        <div class="header-right"><i class="iconfont icon-wode"></i></div>
    </div>
    <div class="main">
      <router-view/>
    </div>
      <mt-tabbar v-model="el">
        <mt-tab-item :id="item.path" v-for="item in banner" :key="item.path">
        <router-link :to="item.path" class='nohao'>
          <div  class="ico"><i :class="iconname(item)"></i></div>
          {{item.title}}
        </router-link>
        </mt-tab-item>
      </mt-tabbar>
    </div>
</template>

<script>
import routes from '@/router/routes'
import { mapState } from 'vuex'
export default {
  name: 'App',
  computed: {
    ...mapState(['selected', 'hastabbar']),
    banner () {
      return routes.filter(item => item.istabbar === true)
    },
    iconname () {
      return item => `iconfont icon-${item.icon}`
    },
    el: {
      get () {
        return this.selected
      },
      set (val) {
        this.$store.state.selected = val
        return this.$store.state.selected
      }

    }
  },
  updated () {
    console.log('/' + this.$router.history.current.path.split('/')[1])
    this.$store.state.selected = '/' + this.$router.history.current.path.split('/')[1]
  }
}
</script>

<style lang="scss" scoped>
#app{
  background:#eee;
.header{
    height:52px;
    background:#1c9b31;
    display:flex;
    z-index: 999;
    color: #fff;
    line-height: 52px;
    position: fixed;
    right: 0;
    left: 0;
    &-title{
      flex:1;
      display:flex;
      justify-content:center;
      align-items:center;
      .wrap{
        background:#fff;
        width:100%;
        height:32px;
        border-radius:5px;
        display:flex;
        justify-content:flex-end;
        align-items:center;
        .search{
          width:85%;
          margin-right:4px;
          background:#fff;
          height:30px;
          border:none;
          outline:none;
        }
        .icon-search{
          position:relative;
          left:-7px;
          color:black;
      }
      }
    }
    &-left,
    &-right{
      padding:0 15px;
    }
  }
  .ico{
    padding-bottom:10px;
    color:black;
  }
  .main{
    height:100vh;
    padding-top:52px;
  }
  .mint-tabbar{
    position:fixed;
    right:0;
    left:0;
    bottom:0;
  }
  .nohao{
    color:black;
  }
}
</style>



